/* Name bar */
.name-bar{
  width: 90vw;
  height: 10vh;
  margin: 0;
  padding: 5vmin;
  /* background: #ddd; */
  position: relative;
  font: Bold 5vw/10vh fantasy;
  color: #fff;
  -webkit-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
  -moz-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
  -ms-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
  filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
}
.level-circle{
  position: absolute;
  background: #c8eeed;
  width: 20vw;
  height: 20vw;
  text-align: center;  /* 円の中心に文字を置く */
  line-height: 20vw;   /* 円の中心に文字を置く2 heightと合わせる  */
  border-radius: 50%;
  float: left;
  -webkit-filter:drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.2));
  -moz-filter:drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.2));
  -ms-filter:drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.2));
  filter:drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.2));
}
.name-container{
  text-align: center;
  position: absolute;
  width: 80vw;
  height: 10vh;
  border-top-left-radius: 5vh;
  border-bottom-left-radius: 5vh;
  background: #80e0df;
  float: left;
  margin-top: 3vh;
  margin-left: 3vw;
}
/* // Name bar */