/* Room */
.room{
  background: #ccddee;
  position: relative;
}
  /* Character */
  .character{
    position: absolute;
    top: 10vh;
    left: 0;
  }

/* Mode Text */
 .mode-text{
   position: relative;
   top:55vh;
   left: 40vw;
   background-color: rgba(100, 200, 210, 0.4);
   padding-top: 1vh;
   padding-bottom: 1vh;
   padding-left: 5vw;
   padding-right: 5vw;
   color: #fff;
 }


 /* Dialog */
 .dialog{
  width: 90vw;
  height: 85vh;
  background-color: #fefefe;
  z-index: 2;
  border: 0;
  -webkit-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
  -moz-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
  -ms-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
  filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
 }
 .dialog-close{
  width: 10vw;
  height: 10vh;
  background-color: rgba(0, 0, 0, 0);
  border:0px solid #aaa;
 }
 .dialog-close:hover{
   background-color: #eeeeee;
 }
 .dialog-close-icon{
   pointer-events: none;
 }
 .dialog-tmp{
   font-size: 4vw;
   margin-top: 30vh;
   text-align: center;
 }
