/* Menu Bar */
.menu-bar{
  position: fixed;;
  top: 80vh;
  width: 90vw;
  height: 10vh;
  margin-left: 5vw;
  margin-right: 2vw;
  -webkit-filter:drop-shadow(4px 5px 3px rgba(0, 0, 0, 0.2));
  -moz-filter:drop-shadow(4px 5px 3px rgba(0, 0, 0, 0.2));
  -ms-filter:drop-shadow(4px 5px 3px rgba(0, 0, 0, 0.2));
  filter:drop-shadow(4px 5px 3px rgba(0, 0, 0, 0.2));
 }
 .menu-item{
  float: left;
  width: 22.5vw;
  padding-top: 2vh;
  padding-bottom: 1.5vh;
  background-color: #80d0d0;
  border: 0;
 }
 .menu-item:hover{
   background-color: #9aeaea;
 }
 .icon-image{
  width: 10vw;
  height: 10vh;
  pointer-events: none;
 }
 .icon-text{
  font-size: 2em;
  font-family: 'Century Gothic';
  color: #fff;
  text-align: center;
  pointer-events: none;
 }