.title{
    font-size: 5vw;
    font-family: sans-serif;
    color: #aaaaaa;

    margin-left: 15vw;
}
.items{
    width: 80vw;
    height: 50vh;
    overflow-y: scroll;
    margin-left: 5vw;
    margin-top: 3vh;
    padding-left: 2vw;
    padding-top: 5vh;
}
.item{
    float: left;
    width: 22vw;
    height: 20vw;
    background-color: rgb(240, 250, 255);
    margin: 1.8vmin;
    border: 0;
    -webkit-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
    -moz-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
    -ms-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
    filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
}
.item-name{
    font-size: 2vmin;
    color: #aaaaaa;
}
.item-img img{
    width: 10vw;
    height: 10vw;
}
.details{
    padding-top: 2vw;
    padding-left: 10vw;
}
.detail-icon{
    width: 10vw;
    height: 10vw;
    float: left;
}
.present-button{
    clear: both;
    margin-left: 70vw;
}
.present-button button{
    width: 10vw;
    height: 5vh;
    background-color: #aaddee;
    border: 0;
    color: white;
}
.detail-main{
    float: left;
    margin-left: 5vw;
}
.detail-name{
    max-width: 40vw;
    font: normal 2vw fantasy;
}
.detail-text{
    max-width: 40vw;
    white-space: pre-line;    
}